<template>
    <div v-show="!isSingleMode" class="groupBox">
       
        <div class="groupSelect">
            <span class="m-r-5">{{$t('common.groupList')}}</span>
            <el-select v-model="groupValue" ref="devId" class="m-t-10" @change = "groupChange" size="small">
                <el-option
                    v-for="item in groupOptions"
                    :key="item.name"
                    :label="item.name"
                    :value="item.name">
                    <span style="float: left">
                        <i class="fa fa-windows m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.windows"></i>
                        <i class="fa fa-android m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.android"></i>
                        <i class="fa fa-linux m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.linux"></i>
                        <i class="fa fa-object-group m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.mix"></i>
                        {{ item.name}}
                    </span>
                </el-option>
            </el-select>
        </div>
        <div class="groupSelect">
            <span class="m-r-5">{{$t('common.standardDevice')}}</span>
            <div class="input">
                <el-input v-model="standardDevice" disabled size="small" ></el-input>
            </div>
        </div>
    </div>
    
</template>

<script>
    import {getDeviceGroupApi, getDeviceGroupByTypeApi, getDeviceGroupByTypeListApi} from '../components/restfulapi/deviceGroupApi'
    import {getDeviceApi, getOnlineDeviceByGroupApi} from '../components/restfulapi/deviceApi'
    import handleResponse from '../components/restfulapi/handleResponse'
    import {setSession, getSession, checkSession} from "../assets/js/storage"
    import {groupType} from "../assets/js/constant"

    export default{
        name: 'selectGroup',

        data(){
            return {
                groupValue:'',
                groupOptions: [],
                groupTypeArray: groupType,
                standardDevice: ""
            }
        },
        props: {
            isSingleMode: {
                type: Boolean,
                default: true,
            },

            //groupType
            groupType: {
                type: Array,
                default: ()=>[groupType.linux, groupType.windows, groupType.android, groupType.mix]
            }
        },
        methods:{
            getAllDeviceGroups(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                getDeviceGroupByTypeListApi(this.groupType).then((data) => {
                    handleResponse(data, (res) => { 
                        if(Object.prototype.toString.call(res)  === '[object Array]'){
                            this.groupOptions = res;
                            if(checkSession("selectedGroup")){
                                let groupValue = getSession("selectedGroup");
                                this.groupOptions.forEach((item) => {
                                    if(this.groupType.includes(item.type)&& item.name == groupValue){
                                        this.groupValue = groupValue;
                                        this.groupChange(this.groupValue);
                                    }
                                })
                            }
                        }
                    })
                })
            },
            groupChange(val){
                this.deviceOptions = [];
                this.standardDevice = "";
                this.getAllDevices(val);
                this.groupOptions.forEach((item) => {
                    if(val == item.name){
                        this.$emit("groupType", item.type);
                    }
                })       
                setSession("selectedGroup", val);
            },
            getAllDevices(gname){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                getOnlineDeviceByGroupApi(gname).then((data) => {
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res) === "[object Array]"){
                            if(res.length > 0){
                                let options = [];
                                res.forEach((val) => {
                                    if(val.isonline){
                                        options.push({value: val.endpoint, label: val.devName})
                                    }
                                })
                                this.standardDevice = options[0].label;
                                this.$emit("select-getDeviceOptions", options);
                            }
                            this.$emit("select-getGroupName", gname);
                        }else{
                            console.error("data is not array")
                        }
                       
                    })
                })
            },

        },
        created(){
            if(!this.isSingleMode){
                Object.assign(this.$data, this.$options.data());
                this.getAllDeviceGroups();
            }
            
        },
        
        watch:{
            isSingleMode(val){
                if(!val){
                    this.getAllDeviceGroups();
                }
            },
        }

    }
</script>
<style lang='scss' scoped>
    @import "../assets/css/colors";
    .groupBox{
        display: inline-block;
        .groupSelect{
            display: inline-block;
            margin-left: 10px;
            span{
                display: inline-block;
            }
            .input{
                display: inline-block;
            }
        }
    }
    
    
</style>




