import {apiGet, apiPut, apiPost} from "../../assets/js/baseApi";

let getAllDeviceStatus = function() {
    return new Promise((resolve, reject) => {
        apiGet("/api/devicemgr/alldevicestatus").then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
}

let getAllDevicesOs = function() {
    return new Promise((resolve, reject) => {
        apiGet("/api/devicemgr/alldevicesos").then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
}

let getScreenshotImg = function(agentId) {
    return new Promise((resolve, reject) => {
        apiGet("/api/devicemgr/screenshotimg/"+agentId).then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
}

let getOnlineDeviceAbnormalStatus = function() {
    return new Promise((resolve, reject) => {
        apiGet("/api/devicemgr/onlinedeviceabnormalstatus").then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
}

let getDeviceStatus = function(agentId, sensorId, data){
    return new Promise((resolve, reject) => {
        let url = "/api/devicemgr/"+ agentId+ sensorId
        apiGet(url, data).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

let setDeviceStatus = function(agentId, sensorId, val="", isSync=false){
    if(isSync){
        val="sync "+val;
    }
    return new Promise((resolve, reject) => {
        let url = "/api/devicemgr/"+ agentId+ sensorId; 
        let resourceId = parseInt(sensorId.substring(sensorId.lastIndexOf("\/")+1, sensorId.length));
        var obj = {
            id: resourceId,
            value: val
        }
        apiPut(url, obj).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

let execDeviceStatus = function(agentId, sensorId, dpData){
    return new Promise((resolve, reject) => {
        let url = "/api/devicemgr/"+ agentId+ sensorId;
        let config = {};
        if(dpData){
            config = {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                },
            };
        }else{
            dpData = {};
        }
        
        apiPost(url, dpData, config).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}
let reExecDeviceStatus = function(agentId, sensorId, dpData){
    return new Promise((resolve, reject) => {
        let url = "/api/devicemgr/redeploy"+ sensorId;
        let config = {};
        if(dpData){
            dpData.endpoint = agentId;
            config = {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                },
            };
        }else{
            dpData = {};
        }
        
        apiPost(url, dpData, config).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

let batchExecDeviceStatus = function(sensorId, dpData){
    return new Promise((resolve, reject) => {
        let url = "/api/devicemgr/batchdeploy"+sensorId;
        let config = {};
        if(dpData){
            config = {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                },
            };
        }else{
            dpData = {};
        }
        
        apiPost(url, dpData, config).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

export{
    getDeviceStatus,
    setDeviceStatus,
    execDeviceStatus,
    batchExecDeviceStatus,
    reExecDeviceStatus,
    getAllDeviceStatus,
    getOnlineDeviceAbnormalStatus,
    getAllDevicesOs,
    getScreenshotImg,
}


