<template>
    <div class="switch">
        <span class="switch-label">
            {{$t('common.batch')}}
        </span>
        <el-switch
        class="text-center m-t-20"
        style="display: block;color:#fff"
        v-model="mode"
        @change="modeChange"
        inactive-color="#ddd"
        active-color="rgb(255, 191, 0)"
        >
        </el-switch>
    </div>
    
</template>
<script>
import {setSession, getSession, checkSession} from "../assets/js/storage"
export default {
    name: 'modeSwitch',
    data(){
        return {
            mode: false
        }
    },
    methods: {
        modeChange(val){
            setSession("mode", val);
            this.$emit("mode-status", val);
        },

        initData(){
            if(checkSession("mode")){
                let mode = getSession("mode");
                this.mode = mode == "true" ? true: false;
            }
            this.modeChange(this.mode);
        }
        
    },

    created(){
        this.initData();
    }
}
</script>
<style lang="scss" scoped>
    .switch{
        display: inline-block;
        .el-switch{
            display: inline-block !important;
            margin-top: -2px!important;
        }
        .switch-label{
            color: #fff;
            margin-right: 5px;
            display: inline-block;

        }
    }

</style>


