import {apiGet, apiPut, apiPost, apiDelete} from "../../assets/js/baseApi";
import {pluginname} from "../../assets/js/constant";


let getDeviceStatus = function(agentId, param){
    return new Promise((resolve, reject) => {
        let url = "/api/plugins/"+ agentId+ "/40008/0/27600";
        var obj = {
            pluginname: pluginname,
            actions: param
        }
        apiGet(url, obj).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

let setDeviceStatus = function(agentId, param){
    param.actions = JSON.stringify(param.actions); 
    return new Promise((resolve, reject) => {
        let url = "/api/plugins/"+ agentId+ "/40008/0/27601";
        var obj = {
            id: 27601,
            value: JSON.stringify(param)
        }
        apiPut(url, obj).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

