let batchDeletePluginsStatusHistoryApi = function(psids){
    let psidsJson = JSON.stringify(psids);
    let data = {
        psids: psidsJson
    }
    let config = {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        },
    };
    return new Promise((resolve, reject) => {
        apiPost("/api/plugins/batchdelete", data, config).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

