<template>
    <div class="p-r cf" v-loading="listLoading" v-show="!isSingleMode">
        <el-col :span="12">
            <div class="docker-box cf m-r-10" @click="goRouter('compose')">
                <div class="left_area">
                    <div class="widget">
                        <i class="fa fa-th-list icon"></i>
                    </div>
                </div>
                <div class="title">{{compose_total}}</div>
                <div class="comment">Stacks</div>
                <div class="right_area">

                </div>
            </div>
        </el-col>
        <el-col :span="12">
            <div class="docker-box cf" @click="goRouter('container')" >
                <div class="left_area">
                    <div class="widget">
                        <i class="fa fa-server icon"></i>
                    </div>
                </div>
                <div class="right_area">
                    <div>
                        <i class="fa fa-power-off running-icon"></i>{{container_running}} {{$t('dockerControl.running')}}
                    </div>
                    <div>
                        <i class="fa fa-power-off stop-icon"></i>{{container_stoped}} {{$t('dockerControl.stoped')}}
                    </div>
                </div>
                <div class="title">{{container_total}}</div>
                <div class="comment">{{$t('dockerControl.containers')}}</div>
                
            </div>
        </el-col>
        <el-col :span="12">
            <div class="docker-box cf m-r-10" @click="goRouter('images')">
                <div class="left_area">
                    <div class="widget">
                        <i class="fa fa-clone icon"></i> 
                    </div>
                </div>
                <div class="right_area">
                    <i class="fa fa-pie-chart volume-icon"></i>
                    {{image_usaged}}
                </div>
                <div class="title">{{image_total}}</div>
                <div class="comment">images</div>

            </div>
        </el-col>
    </div>
</template>


<script>
    import {dockerAction} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';

    export default{
        name: 'dockercontrol',
        data(){
            return {
                image_total: "0",
                image_usaged: "0G",
                container_total: "0",
                container_running: "0",
                container_stoped: "0",
                compose_total: "0",
                listLoading: false,
                // [{"Name":"compose","Total":"1","Running":"","Stopped":"","Usaged":""},{"Name":"container","Total":"5","Running":"3","Stopped":"2","Usaged":""},{"Name":"image","Total":"5","Running":"","Stopped":"","Usaged":"3.3G"}]
            }
        },

        props:{
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },

        methods:{
            getSensorStatus(){
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, dockerAction["getInfo"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                this.dockerInfo = JSON.parse(res.content.value);
                                this.dockerInfo.forEach(el => {
                                    if(el.name == "compose"){
                                        this.compose_total = el.total;
                                    }else if(el.name == "container"){
                                        this.container_total = el.total;
                                        this.container_running = el.running;
                                        this.container_stoped = el.stoped;
                                    }else{
                                        this.image_total = el.total;
                                        this.image_usaged = el.usaged;
                                    }
                                })
                            }
                            
                        }
                    })   
                })
            },


            initData(){
                this.image_total="0";
                this.image_usaged= "0G";
                this.container_total= "0";
                this.container_running= "0";
                this.container_stoped= "0";
                this.compose_total= "0";
            },


            goRouter(cid){
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                if(cid == "compose"){
                    this.$router.push({path:"/main/control/composeControl"});
                }else if(cid == "container"){
                    this.$router.push({path:"/main/control/containerControl"});
                }else if(cid == "images"){
                    this.$router.push({path:"/main/control/imageControl"});
                }
            }
        },
        watch: { 
            isSingleMode(val){
                if(!val){
                    this.initData();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.initData();
                    this.getSensorStatus();
                }
            }
        },
    }
</script>

<style lang="scss" scoped>
    .docker-box:hover{
        .title{
            text-decoration: underline;
        }
        .comment{
            text-decoration: underline;
        }
    }
    .docker-box{
        padding:20px;
        box-sizing: border-box;
        background: #fff;
        margin-top: 20px;
        color: #337ab7;
        font-weight: 500;
        font-family: Montserrat;
        cursor: pointer;
        .left_area{
            float: left;
            .widget{
                // display: inline-block;
                width: 65px;
                height: 65px;
                border-radius: 50%;
                text-align: center;
                background: #2361ae;
                vertical-align: middle;
                margin-right: 15px;
                .icon{
                    line-height: 66px;
                    color: #fff;
                    font-size: 30px;
                }
            }
        }
        .title{
            font-size: 28px;
            height:50%;
        }
        .comment{
            margin-top:5px;
            font-size: 16px;
            height:50%;
        }
        .right_area{
            float: right;
            
            font-size: 14px;
            margin-right: 5px;
            .running-icon{
                
                color: #23ae89;
                margin-right: 5px;
            }
            .stop-icon{
                color: #ae2323;
                margin-right: 5px;
            }
            .volume-icon{
                columns: #337ab7;
            }
        }
    }
</style>



