import {repoApiGet} from '@/assets/js/baseApi'
import {getRepoServerApi} from "../restfulapi/serverConfigApi"
import {getToken} from "./getRepoTokenApi"

let getRepoAppsApi = function(){
    return new Promise((resolve, reject) => {
        getRepoServerApi().then((repoUrl) => {
            let getRepoAppUrl =  repoUrl + "/repoapps";
            repoApiGet(getRepoAppUrl, "").then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        })
    })
}

let getRepoApi = function(reponame){
    return new Promise((resolve, reject) => {
        getRepoServerApi().then((repoUrl) => {
            let getDarknameUrl =  repoUrl + "/repo/"+reponame;
            repoApiGet(getDarknameUrl, "").then((data) =>{
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
            
        })
    })
}

export {
    getRepoAppsApi,
    getRepoApi
}


