<template>
    <div class="deviceBox" v-show="isSingleMode">
        <div class="groupSelect">
            <span class="m-r-5">{{$t('common.groupList')}}</span>
            <el-select v-model="groupValue" ref="devId" class="m-t-10" @change = "groupChange" size="small">
                <el-option
                    v-for="item in groupOptions"
                    :key="item.name"
                    :label="item.name"
                    :value="item.name">
                    <span style="float: left">
                        <i class="fa fa-windows m-r-5" aria-hidden="true" v-if="item.type== groupTypes.windows"></i>
                        <i class="fa fa-android m-r-5" aria-hidden="true" v-if="item.type== groupTypes.android"></i>
                        <i class="fa fa-linux m-r-5" aria-hidden="true" v-if="item.type== groupTypes.linux"></i>
                        <i class="fa fa-object-group m-r-5" aria-hidden="true" v-if="item.type== groupTypes.mix"></i>
                        {{ item.name}}
                    </span>
                    
                </el-option>
            </el-select>
        </div>
 
        <div class="deviceSelect">
            <span  class="m-r-5">{{$t('common.deviceList')}}</span>
            <el-select v-model="devValue" ref="devId" class="m-t-10" size="small" filterable>
                <el-option-group
                    v-for="group in deviceOptions"
                    :key="group.label"
                    :label="group.label"
                    :disabled="group.label == $t('common.offline')&& isDisabledOffDev"
                    >
                    <el-option
                        v-for="item in group.options"
                        :key="item.endpoint"
                        :label="item.devName"
                        :value="item.endpoint"
                        :disabled="(item.os != groupType&& groupType != '')||(groupTypeArray.indexOf(item.os)==-1&& groupTypeArray.length!=0)">
                        
                        <span style="float: left">
                            <i class="fa fa-windows m-r-5" aria-hidden="true" v-if="item.os== groupTypes.windows"></i>
                            <i class="fa fa-android m-r-5" aria-hidden="true" v-if="item.os== groupTypes.android"></i>
                            <i class="fa fa-linux m-r-5" aria-hidden="true" v-if="item.os== groupTypes.linux"></i>
                            {{ item.devName}}
                        </span>
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
    </div>  
</template>
<style lang="scss" scoped>
    .deviceBox{
        display: inline-block;
        .groupSelect,.deviceSelect{
            display: inline-block;
            margin-left: 10px;
        }
    }
</style>


<script>
    import {getDeviceApi, classifyDeviceApi, classifyDeviceInGroupApi,classifyDeviceInGroupAndOsApi} from '../components/restfulapi/deviceApi'
    import {getDeviceGroupApi, getDeviceGroupByTypeApi, getDeviceGroupByTypeListApi} from '../components/restfulapi/deviceGroupApi'
    import handleResponse from '../components/restfulapi/handleResponse'
    import {setSession, getSession, checkSession} from "../assets/js/storage"
    import {mapState} from 'vuex'
    import {groupType} from "../assets/js/constant"


    export default{
        name: 'selectDevice',
        data(){
            return {
                devValue: '',
                deviceOptions: [],
                offlineDevices: [],
                onlineDevices: [],
                groupValue: "",
                groupOptions: [],
                groupTypes: groupType
            }
        },
        props: {
            isSingleMode: {
                type: Boolean,
                default: true,
            },

            //is disabled select offline devlce
            isDisabledOffDev: {
                type: Boolean,
                default: true
            },

            //group type
            groupType: {
                type: String,
                default: "",
            },

            groupTypeArray: {
                type: Array,
                default: ()=> {
                    return [];
                }
            }
        },
        methods:{
            getAllDeviceGroups(){
                if(this.groupTypeArray.length == 0){
                    if(this.groupType){
                        getDeviceGroupByTypeListApi([this.groupType, groupType.mix]).then((data) => {
                            handleResponse(data, (res) => { 
                                if(Object.prototype.toString.call(res)  === '[object Array]'){
                                    this.groupOptions = res;
                                    let groupValue = "";
                                    if(checkSession("selectedDeviceGroup")){
                                        groupValue = getSession("selectedDeviceGroup");
                                    }
                                    this.groupOptions.forEach((item) => {
                                        if((item.type == this.groupType|| item.type == groupType.mix)&& item.name == groupValue){
                                            this.groupValue = groupValue;
                                            this.groupChange();
                                        }
                                    })
                                }
                            })
                        })
                    }else{
                        getDeviceGroupApi().then((data) => {
                            handleResponse(data, (res) => { 
                                if(Object.prototype.toString.call(res)  === '[object Array]'){
                                    this.groupOptions = res;
                                    let groupValue = "";
                                    if(checkSession("selectedDeviceGroup")){
                                        groupValue = getSession("selectedDeviceGroup");
                                    }
                                    this.groupOptions.forEach((item) => {
                                        if((item.type == this.groupType|| item.type == groupType.mix|| this.groupType == "")&& item.name == groupValue){
                                            this.groupValue = groupValue;
                                            this.groupChange();
                                        }
                                    })
                                    
                                }
                            })
                        }) 
                    }
                }else{
                    this.groupTypeArray.push(groupType.mix);
                    getDeviceGroupByTypeListApi(this.groupTypeArray).then((data) => {
                        handleResponse(data, (res) => { 
                            if(Object.prototype.toString.call(res)  === '[object Array]'){
                                this.groupOptions = res;
                                let groupValue = "";
                                if(checkSession("selectedDeviceGroup")){
                                    groupValue = getSession("selectedDeviceGroup");
                                }
                                this.groupOptions.forEach((item) => {
                                    if((this.groupTypeArray.indexOf(item.type)>-1)&& item.name == groupValue){
                                        this.groupValue = groupValue;
                                        this.groupChange();
                                    }
                                })
                            }
                        })
                    })
                }
            },
            groupChange(){
                this.deviceOptions = [];
                this.devValue = "";
                this.getAllDevices();
                setSession("selectedDeviceGroup", this.groupValue);
            },
            
            getAllDevices(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(!this.groupValue){
                    console.error("No device group")
                    return;
                }
                classifyDeviceInGroupApi(this.groupValue).then((data) => {
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res.online) === "[object Array]"){
                            let defaultDeviceAgent = getSession("selectedAgentId");
                            let onlineDevices = res.online;
                            onlineDevices.forEach((val) => {
                                if((((val.os== this.groupType||this.groupType == "")&& this.groupTypeArray.length == 0)
                                || (this.groupTypeArray.indexOf(val.os)>-1&& this.groupTypeArray.length>0))
                                && val.endpoint === defaultDeviceAgent){
                                    this.devValue = defaultDeviceAgent;
                                }
                            })
                            //create offline devices array for emit device status to parent el
                            let offlineDevices = res.offline;
                            let offlineArray = [];
                            offlineDevices.forEach((val) => {
                                offlineArray.push(val.endpoint);
                            })
                            this.offlineDevices = offlineArray;  
                            this.onlineDevices = onlineDevices;

                            this.deviceOptions = [{label: this.$t('common.online'), options: onlineDevices}, {label: this.$t('common.offline'), options: offlineDevices}];
                            
                        }else{
                            console.error("select-device:data is not array")
                        }
                       
                    })
                })
            },
        },
        watch:{
            onlineDeviceCount(){
                if(this.isSingleMode && !this.devValue === ""){
                    Object.assign(this.$data, this.$options.data());
                    this.getAllDeviceGroups();
                }
            },
            isSingleMode(val){
                if(val){
                    Object.assign(this.$data, this.$options.data());
                    this.getAllDeviceGroups();
                }
            },

            devValue(val){
                this.$emit("select-device",val);
                this.onlineDevices.forEach((item) => {
                    if(item.endpoint == val){
                        this.$emit("deviceType", item.os);
                        this.$emit("deviceName", item.devName);
                    }
                })
                //selectedAgent is offline
                // console.log(this.offlineDevices)
                this.$emit("isOfflineDev", this.offlineDevices.indexOf(val)>-1)

                if(val&& this.isDisabledOffDev){ 
                    setSession("selectedAgentId", val);
                }
            },
            
            isDisabledOffDev(value){
                if(value && this.offlineDevices.indexOf(this.devValue)>-1){
                    this.devValue = "";
                }
            }
        },
        
        computed:{
            ...mapState({
                onlineDeviceCount: "onlineDeviceCount"
            }),   
        },

        created(){
            if(this.isSingleMode){
                this.getAllDeviceGroups();
            }
        }
    }
</script>





